@import "../../global.scss";

.intro {
  background-color: white;
  display: flex;

  @include mobile {
    flex-direction: column;
    align-items: center;
  }

  .left {
    flex: 5;
    overflow: hidden;
    @include mobile {
      flex: 4;
    }

    .avatar {
      width: 700px;
      height: 700px;
      background-color: #ffa4;
      border-radius: 50%;
      display: flex;
      align-items: flex-end;
      justify-content: center;
      float: right;

      @include mobile {
        align-items: flex-start;
      }

      img {
        height: 90%;

        @include mobile {
          height: 50%;
        }
      }
    }
  }

  .right {
    flex: 5;
    position: relative;
    @include mobile {
      flex: 6;
    }

    .wrapper {
      width: 100%;
      height: 100%;
      padding-left: 50px;
      display: flex;
      flex-direction: column;
      justify-content: center;

      @include mobile {
        padding-left: 0;
        align-items: center;
      }

      h1 {
        font-size: 60px;
        margin-top: 10px;

        @include mobile {
          font-size: 38px;
          margin-top: 0;
        }
      }
      p {
        font-size: 14px;
        margin-bottom: 10px;
      }
      h2 {
        font-size: 35px;
        @include mobile {
          font-size: 25px;
        }
      }
      h3 {
        font-size: 30px;

        @include mobile {
          font-size: 20px;
        }

        span {
          font-size: inherit;
          color: crimson;
        }

        .ityped-cursor {
          animation: blink 1s infinite;
        }

        @keyframes blink {
          50% {
            opacity: 1;
          }
          100% {
            opacity: 0;
          }
        }
      }
    }
    a {
      position: absolute;
      bottom: 10px;
      left: 40%;

      .arrow {
        font-size: 70px;
        color: black;
        animation: arrowBlink 2s infinite;
        @include mobile {
          margin-bottom: 30px;
        }
      }

      @keyframes arrowBlink {
        100% {
          opacity: 0;
        }
      }
    }
  }
}
