@import "../../global.scss";

.works {
  background-color: #344e5c;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  @include mobile {
    margin: 0;
  }

  .arrow {
    height: 30px;
    position: absolute;
    cursor: pointer;

    @include mobile {
      color: black;
      position: absolute;
      bottom: 0;
      margin-bottom: 60px;
    }

    &.left {
      left: 100px;
      transform: rotateY(180deg);
    }
    &.right {
      right: 100px;
    }
  }

  .slider {
    height: 70vh;
    display: flex;
    position: absolute;
    left: 0;
    transition: all 1s ease-in-out;

    .container {
      width: 100vw;
      display: flex;
      align-items: center;
      justify-content: center;

      .item {
        width: 800px;
        height: 100%;
        background-color: #dfe3ee;
        border-radius: 20px;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;

        @include mobile {
          width: 80%;
          height: 60vh;
          flex-direction: column-reverse;
          border-radius: 10px;
        }

        .left {
          flex: 1;
          height: 80%;
          display: flex;
          align-items: center;
          justify-content: center;
          margin-left: 10px;

          @include mobile {
            flex: 6;
          }

          .leftContainer {
            width: 90%;
            height: 80%;
            display: flex;
            flex-direction: column;
            justify-content: space-between;

            @include mobile {
              width: 95%;
              height: 95%;
              margin: 0 25px;
            }

            .imgContainer {
              width: 40px;
              height: 40px;
              border-radius: 50%;
              background-color: coral;
              display: flex;
              align-items: center;
              justify-content: center;

              @include mobile {
                display: none;
              }

              .img {
                width: 25px;
              }
            }
            h2 {
              font-size: 20px;
              @include mobile {
                font-size: 16px;
                margin-bottom: 0;
              }
            }
            span {
              font-size: 12px;
              font-weight: 600;
              text-decoration: underline;

              @include mobile {
                font-size: 12px;
                margin-top: 0;
              }
            }
            p {
              font-size: 13px;

              @include mobile {
                font-size: 12px;
              }
              a {
                text-decoration: underline;
                font-weight: 500;
                color: inherit;
              }
            }
            .YouTube {
              display: flex;
              flex-direction: column;
              padding: 0;
              margin: -50;
              @include mobile {
                margin-bottom: 50px;
              }

              .top {
                display: flex;
                align-items: center;
                padding: 0;
                margin: 0;
                h6 {
                  font-size: 15px;
                  @include mobile {
                    font-size: 12px;
                  }
                }
              }
              .bot {
                padding: 0;
                margin: -50;
                .youTube {
                  display: flex;
                  flex-direction: row;
                  align-items: center;
                  p {
                    font-size: 15px;
                    a {
                      text-decoration: none;
                      color: inherit;
                      @include mobile {
                        font-size: 10px;
                      }
                    }
                  }
                }
              }
            }
          }
        }

        .right {
          flex: 1;
          height: 100%;
          display: flex;
          align-items: center;
          justify-content: center;
          overflow: hidden;
          padding-right: 20px;

          @include mobile {
            flex: 4;
            height: 90%;
            margin-right: 0;
            padding: 5px;
          }

          img {
            width: 400px;
            border: 2px solid gray;
            border-radius: 10px;
            box-shadow: 0.3rem 0.3rem 0.65rem 0.2rem darkgray;
            @include mobile {
              width: 250px;
              height: 120px;
              margin-top: 10px;
              border: 2px solid gray;
              border-radius: 10px;
              box-shadow: 0.1rem 0.1rem 0.25rem 0.2rem darkgray;
            }
          }
        }
      }
    }
  }
}
